@import './reset.scss';

$메인칼라 : #d3313a;
$서브칼라 : #333;

.red{
    color: $메인칼라;
    margin-bottom: 20px;
    font-size:24px;
    font-weight: 500;
}
.my-alert{
    padding:20px 0;
    border-radius: 5px;
    max-width: 500px;
    width:100%;
    margin:0 auto;
    visibility:visible;
    p{
        color:$서브칼라;
        font-weight: 500;
        font-size:15px;
    }
}
.col-md-6{
    .pt-5{
        margin-top:0;
        padding-top:0;
    }
    img{
        width:40%;
    }
    .pt-5{
        font-size:20px;
        font-weight: 600;
        color: $서브칼라;
        margin-bottom: 10px;
    }
    p{
        font-size:14px;
        color:$서브칼라;
        margin-bottom: 30px;
    }
    .btn{
        margin: 0 2px;
    }
}

.wow-enter{
    opacity: 0;
}
.wow-enter-active{
    opacity: 1;
    transition: all 0.5s;
}