@import './reset.scss';
$maincolor : #d3313a;
$subcolor : #333;
button{
  display: inline-block;
  border:0;
  outline: none;
}
a{
  color:$maincolor;
  font-weight: 500;
}
a:hover{
  color:$maincolor;
}
div.container-fluid{
  width:100%;
}
.logo{
  width:50%;
  text-align: left;
  img{
    width:100%;
  }
}
#navbarScroll{
  outline: none;
}
.btn-outline-success{
  background-color:$maincolor;
  color:#eee;
  border:0px;
}
.btn-outline-success:hover{
  color:#eee;
  background-color: $subcolor;
}
.App {
  text-align: center;
  font-family: 'Noto Sans KR', sans-serif;
}
.container{
  width:100%;
  margin-top:50px;
  hr{
    width:15%;
    position: absolute;
    left:50%;
    transform: translateX(-50%);
  }
  h3{
    font-size:24px;
    color:$maincolor;
    padding-top: 20px;
    font-weight: 600;
  }
  .btn{
    margin:50px auto;
    display: inline-block; 
  }
  
}
div.col-md-6{
  text-align: center;
  img{
    width:40%;
    display: inline-block;
  }
  h4{
    font-size:18px;
    font-weight: 600;
    margin-bottom: 10px;

  }
  p{
    font-size: 13px;
    color:$subcolor;
  }
}

div.alert{
  padding:20px 0;
  border-radius: 5px;
  max-width: 500px;
  width:100%;
  margin:0 auto;
  visibility:visible;
  p{
      color:$subcolor;
      font-weight: 700;
      font-size:24px;
  }
}

div.copy{
  position: relative;
  padding:100px 0 50px;
  img{
    width:40%;
    margin-bottom:10px;
  }
  p{
    font-size: 13px;
    color:$subcolor;
  }
}
.topbtn{
  width:20%;
  position: absolute;
  right:5%;
  top:50%;
  background: none;
  cursor: pointer;
}

.accordion-button:not(.collapsed){
  color:$maincolor;
}